<template>
  <div>
    <h1>Pokemon Detail</h1>
    <v-subheader>
      <router-link class="mr-2" to="/" active-class="active" exact tag="a">&lt;&lt; Back to Pokedex</router-link>
    </v-subheader>
    <v-container fluid>
      <v-row align="center" justify="center" class="grey lighten-5">
        <v-card max-width="344" class="mx-auto">
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title class="headline text-capitalize">{{pokemonData.name}}</v-list-item-title>
              <v-list-item-subtitle>Height: {{pokemonData.height}} | Weight: {{pokemonData.weight}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-img v-if="front_default" :src="front_default" height="auto"></v-img>
          <v-card-text v-else>No image available</v-card-text>

          <v-card-text>
            <h5>Moves</h5>
            <v-btn
              text
              class="w-50 justify-content-start"
              style="cursor: default"
              v-for="(move, index) of moves"
              :key="index"
            >{{move}}</v-btn>
          </v-card-text>

          <v-card-actions class="justify-content-center">
            <v-dialog v-model="dialog" width="500">
              <template v-slot:activator="{ on }">
                <v-btn text color="deep-purple accent-4 w-100" @click="tryToCatch">Catch!</v-btn>
              </template>

              <v-card>
                <v-subheader>{{message}}</v-subheader>
                <v-card-title
                  class="headline grey lighten-2"
                  primary-title
                >Input a name (max 10 character)</v-card-title>

                <v-card-text>
                  <v-text-field :counter="10" v-model="nickname" label="Nickname" required />
                </v-card-text>

                <v-card-actions>
                  <div class="flex-grow-1"></div>
                  <v-btn color="primary" text @click="addPokemon()">Submit</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-actions>
          <div class="d-flex justify-content-center">
            <span class="text-danger" v-if="message === 'Catch Failed!'">{{message}}</span>
          </div>
        </v-card>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Axios from "axios";
import { myPokemon } from "../singletons/myPokemon";
export default {
  created() {
    Axios.get(`https://pokeapi.co/api/v2/pokemon/${this.id}`).then(res => {
      this.pokemonData = res.data;
      this.setMoves();
    });
  },
  data() {
    return {
      id: this.$route.params.id,
      pokemonData: {},
      moves: [],
      nickname: "",
      dialog: false,
      message: ""
    };
  },
  computed: {
    front_default() {
      return this.pokemonData ? this.pokemonData.sprites.front_default : "";
    }
  },
  methods: {
    addPokemon() {
      myPokemon.push({
        name: this.pokemonData.name,
        nickname: this.nickname,
        img: this.front_default,
        moves: this.moves
      });

      this.dialog = false;
      this.$router.push("/");
    },
    tryToCatch() {
      let isSuccess = Math.ceil(Math.random() * 100) < 51 ? true : false;
      if (isSuccess) {
        this.message = "Catch Success!";
        this.dialog = true;
      } else {
        this.message = "Catch Failed!";
      }
    },
    setMoves() {
      for (let i = 0; i < 4; i++) {
        let index = Math.floor(Math.random() * this.pokemonData.moves.length);
        this.moves.push(this.pokemonData.moves[index].move.name);
        this.pokemonData.moves.splice(index, 1);
      }
    }
  }
};
</script>