<template>
  <div>
    <h1 class="text-uppercase">Pokedex</h1>
    <span>Total Pokemon: {{myPokemon.length}}</span>
    <v-container fluid>
      <div class="text-center my-3">
        <v-pagination
          v-model="page"
          :length="paginationLength"
          @input="changePage"
        ></v-pagination>
      </div>
      <v-row align="center" justify="center" class="grey lighten-5">
        <v-card
          class="col-lg-2 col-md-3 col-sm-4 col-xs-6 mb-3 mr-1 ml-1"
          tile
          v-for="(entry, index) in pokemonData.results"
          :key="index"
          hover
        >
          <v-card-title class="text-capitalize pr-0">{{entry.name}}</v-card-title>
          <v-card-text>
            <v-btn :to="getUrl(entry.url)">View Detail</v-btn>
          </v-card-text>
        </v-card>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Axios from "axios";
import { myPokemon } from "../singletons/myPokemon";

export default {
  created() {
    Axios.get("https://pokeapi.co/api/v2/pokemon").then(
      res => (this.pokemonData = res.data)
    );
  },
  data: () => {
    return {
      pokemonData: [],
      page: 1,
      myPokemon: myPokemon
    };
  },
  computed: {
    paginationLength() {
      return this.pokemonData.count
        ? Math.ceil(this.pokemonData.count / 20)
        : 1;
    }
  },
  methods: {
    changePage() {
      Axios.get(
        `https://pokeapi.co/api/v2/pokemon?offset=${(this.page - 1) *
          20}&limit=20`
      ).then(res => (this.pokemonData = res.data));
    },
    getUrl(url) {
      if (url) {
        let array = url.split("/");
        return `/detail/${array[6]}`;
      }
      return "";
    }
  }
};
</script>