<template>
  <div>
    <div>
      <h1 class="text-uppercase">My Pokemon</h1>
      <v-subheader class="px-0 pb-6">Click the image to see pokemon's move set!</v-subheader>
    </div>
    <v-container fluid class="w-100">
      <div class="text-center my-3">
        <v-pagination v-model="page" :length="paginationLength" @input="reload"></v-pagination>
        <br />

        <span class="test-danger">{{message}}</span>
      </div>
      <v-row align="center" justify="center" class="grey lighten-5">
        <v-card
          class="col-lg-2 col-md-3 col-sm-4 col-xs-6 mb-3 mr-1 ml-1"
          style="cursor: default"
          tile
          v-for="(entry, index) in shownList"
          :key="index"
          hover
        >
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title class="headline text-capitalize">{{entry.nickname}}</v-list-item-title>
              <v-list-item-subtitle class="text-capitalize">Species: {{entry.name}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on }" class="justify-content-center">
              <v-img
                v-if="entry.img"
                style="cursor: pointer"
                height="150"
                :src="entry.img"
                @click="dialog = true"
              ></v-img>
            <v-card-text v-else style="height: 150px; cursor: pointer" class="d-flex align-items-center justify-content-center" @click="dialog = true">No image available</v-card-text>
            </template>

            <v-card>
              <v-card-title class="headline grey lighten-2" primary-title>Move set</v-card-title>

              <v-card-text>
                <v-btn
                  text
                  class="w-50 justify-content-start"
                  style="cursor: default"
                  v-for="(move, index) of entry.moves"
                  :key="index"
                >{{move}}</v-btn>
              </v-card-text>

              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn color="primary" text @click="close()">Close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-card-actions class="justify-content-center">
            <v-btn text color="deep-purple accent-4 w-100" @click="release(index)">Release</v-btn>
          </v-card-actions>
        </v-card>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { myPokemon } from "../singletons/myPokemon";
export default {
  created() {
    this.reload();
  },
  data: () => {
    return {
      lists: myPokemon,
      shownList: [],
      page: 1,
      message: "",
      dialog: false
    };
  },
  computed: {
    paginationLength() {
      return this.lists ? Math.ceil(this.lists.length / 10) : 1;
    }
  },
  watch: {
    lists: () => {
      this.reload();
    }
  },
  methods: {
    reload() {
      let lastItem =
        this.lists.length < 10 * this.page ? this.lists.length : 10 * this.page;
      let firstItem = (this.page - 1) * 10;
      this.shownList = this.lists.slice(firstItem, lastItem);
    },
    release(i) {
      if (this.lists.length <= 1) {
        this.message = "You need to at least have 1 pokemon.";
        return;
      }

      let index = (this.page - 1) * 10 + i;
      this.lists.splice(index, 1);
      this.shownList.splice(i, 1);

      if (this.shownList.length === 0 && this.page > 1) {
        this.page -= 1;
        this.reload();
      }
      this.message = "";
    },
    close() {
      this.dialog = false;
    }
  },
  mounted() {
    this.reload();
  }
};
</script>